import { h, Component } from 'preact';
import {defaults, Line} from 'preact-chartjs-2';
import request from 'superagent';
import style from './style';

defaults.global.animation = false;
defaults.global.tooltips.position = 'nearest';

let initialState = {
  labels: [],
  datasets: [],
};

const options = {
  responsive: true,
  elements: {
    line: {
      fill: false,
      tension: 0
    }
  },
  responsiveAnimationDuration: 0,
  hover: {
    animationDuration: 0,
  },
  tooltips: {
    mode: 'x-axis'
  },
  scales: {
    xAxes: [
      {
        display: true,
        gridLines: {
          display: false
        },
      }
    ],
    yAxes: [
      // totalHashRate
      // workerHashRate/worker
      {
        type: 'linear',
        display: true,
        position: 'left',
        id: 'y-axis-1',
        gridLines: {
          display: false
        },
      },
      {
        type: 'linear',
        display: true,
        position: 'right',
        id: 'y-axis-2',
        gridLines: {
          display: false
        },
      }
    ]
  }
};


function datasetsByWorkers(workers) {
  const datasets = [
    {
      label: 'Total Hashrate',
      type: 'line',
      data: [],
      fill: false,
      borderColor: '#EC932F',
      backgroundColor: '#EC932F',
      pointBorderColor: '#EC932F',
      pointBackgroundColor: '#EC932F',
      pointHoverBackgroundColor: '#EC932F',
      pointHoverBorderColor: '#EC932F',
      yAxisID: 'y-axis-1'
    },
  ];
  for (var i = 0; i < workers.length; i++) {
    datasets.push({
      label: workers[i]+' (TH/s)',
      type: 'line',
      data: [],
      fill: false,
      backgroundColor: '#71B37C',
      borderColor: '#71B37C',
      hoverBackgroundColor: '#71B37C',
      hoverBorderColor: '#71B37C',
      yAxisID: 'y-axis-2'
    });
  }
  return datasets;
}

function yAxesByWorkerPop(numOfWorkers) {
  const yAxes = [
    {
      type: 'linear',
      display: true,
      position: 'left',
      id: 'y-axis-1',
      gridLines: {
        display: false
      },
    },
  ];

  for (var i = 0; i < numOfWorkers; i++) {
    yAxes.push({
      type: 'linear',
      display: true,
      position: 'right',
      id: 'y-axis-2',
      gridLines: {
        display: false
      },
    });
  }
  
  return yAxes;
}



function processStats(rawStats) {
  let lastDt = '';
  const workerHashRates = {};
  const dtPoints = rawStats.hashrate.concat([]).map(p => {
    const parts = p.created.split('T');
    if (parts[0] !== lastDt) {
      lastDt = parts[0]
      return parts.join(' ').slice(0, -9);
    }
    return parts[1].slice(0, -9);
  });
  const totalHashRate = rawStats.hashrate.map(p => parseInt(p.hashrate / 1e12, 10));

  rawStats.hashrate.forEach(p => {
    for (var key in p.workerHashrates) {
      if (!workerHashRates[key]) {
        workerHashRates[key] = [];
      }
      const hr = Math.ceil(p.workerHashrates[key] / 1e12 * 10) / 10;
      workerHashRates[key].push(hr);
    }
  });

  for (var key in workerHashRates) {
    workerHashRates[key] = workerHashRates[key].reverse();
  }

  return {
    pendingShares: rawStats.pendingShares,
    pendingBalance: rawStats.pendingBalance,
    totalPaid: rawStats.totalPaid,

    dtPoints: dtPoints.reverse(),
    totalHashRate: totalHashRate.reverse(), // y1
    workerHashRates: workerHashRates, // y2
  };
}

function getStats(addr) {
  return new Promise((resolve, reject) => {
    fetch('http://api.notbcash.org/api/pool/bcl0/miner/'+addr+'/stats')
      .then(r => r.json())
      .then(res => {
        // console.log('RES:', res, typeof res);
        if (res) {
          try {
            resolve(processStats(res));
          } catch (e) {
            console.log('[ERR][getStats][JSON]', e);   
            return reject(e);
          }
        }
      })
      .catch(err => {
        return console.log('[ERR][getStats]', err);
      });
  });
}


export default class Miner extends Component {
  state = datasetsByWorkers([]); //initialState; //{ dtPoints: [], miners: [], hashRates: [] };

  componentWillMount() {
    this.refreshData().then(() => {
      this.loopStats();
    });
  }

  // gets called just before navigating away from the route
  componentWillUnmount() {
    clearInterval(this.timer);
    if (this.refs && this.refs.chartminer && this.refs.chartminer.chart_instance) {
      this.refs.chartminer.chart_instance.destroy();
    }
  }

  loopStats() {
    this.timer = setTimeout(() => {
      if (document.hidden === false) {
        requestAnimationFrame(this.refreshData.bind(this, true));
      } else {
        this.loopStats();
      }
    }, 10 * 60 * 1e3);
  }

  refreshData(isLoop) {
    return getStats(this.props.address).then(stats => {
      if (!stats) return console.log('STOP');
      // Misc data
      initialState._extradata = {};
      initialState._extradata.pendingShares = stats.pendingShares;
      initialState._extradata.pendingBalance = stats.pendingBalance;
      initialState._extradata.totalPaid = stats.totalPaid;

      // Chart data
      options.scales.yAxes = yAxesByWorkerPop(stats.workerHashRates.length);
      // data
      initialState.labels = stats.dtPoints;
      const workerKeys = Object.keys(stats.workerHashRates);
      // Loop workers and add datasets
      initialState.datasets = datasetsByWorkers(workerKeys);
      initialState.datasets[0].data = stats.totalHashRate;

      let datasetIndex = 0;
      workerKeys.forEach(wkey => {
        initialState.datasets[++datasetIndex].data = stats.workerHashRates[wkey];
      });
      // console.log('dataset[0].length', stats.totalHashRate.length, stats.totalHashRate);

      this.setState(initialState);
      if (isLoop) {
        setTimeout(() => this.loopStats(), 1);
      }
    });
  }

  // Note: `address` comes from the URL, courtesy of our router
  render({ address }, state) {
    const { pendingShares, pendingBalance, totalPaid } = state._extradata || {};

    return (
      <div class={style.miner}>
        <h1>Miner's address: {address}</h1>
        <dl>
          <dt>Pending Shares</dt><dd>{pendingShares}</dd>
          <dt>Pending Balance</dt><dd>{pendingBalance} BCL</dd>
          <dt>Total Paid</dt><dd>{totalPaid} BCL</dd>
        </dl>
        <Line data={state} options={options} />
      </div>
    );
  }
}



